#video {
  background: $mainColor;
  .video-wrapper {
    width: $container;
    margin: auto;
    padding: $gutterWidth 0;
    iframe {
      border: none; }
    .video1 {
      grid-area: video1; }
    .video2 {
      grid-area: video2;
      align-self: stretch;
      justify-self: stretch; }
    .video3 {
      grid-area: video3; }
    .video4 {
      grid-area: video4; }
    .video5 {
      grid-area: video5; }

    display: grid;
    grid-gap: $gutterWidth;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: auto;
    grid-template-areas: "video1 video2 video3" "video4 video2 video5"; } }
