.main-header {
  height: 100vh;
  background: {
    image: url('../../../../dist/assets/img/header-background.jpg');
    position: center center;
    repeat: no-repeat;
    size: cover; }

  .caret-down {
    color: #fff;
    font-size: 3rem;
    position: absolute;
    bottom: 5%;
    left: 50%;
    @include transition;
    width: 50px;
    height: 50px;
    @include flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; } }
